.col {
  display: block;
  float: left;
  width: 100%;

  &--sm2 {
    @include sm {
      width: 50%; } }
  &--sm4 {
    @include sm {
      width: 25%; } }

  &--md2 {
    @include md {
      width: 50%; } }
  &--md4 {
    @include md {
      width: 25%; } }

  &--lg2 {
    @include lg {
      width: 50%;
      &--right {
        margin-left: 50%; } } }
  &--lg4 {
    @include lg {
      width: 25%; } }

  &--fullHeight {
    @include lg {
      min-height: 100vh; }
    &FixedLeft {
      @include lg {
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        overflow: hidden;
        z-index: 7; } } } }

