.commentBox {
  text-align: center;
  margin: 3rem;
  color: $colorWhite;

  &__heading {
    font-size: 1.6rem;
    font-weight: 600;
    @include sm {
      font-size: 1.2rem; }
    @include lg {
      font-size: 1.6rem; } }

  &__quote {
    color: $colorWhite;
    &::before {
      content: '';
      display: block;
      width: 3rem;
      height: 3rem;
      margin: 2rem auto;
      background: url(../img/quote.svg) center center no-repeat;
      @include sm {
        display: none; }
      @include lg {
        display: block; } }

    @include sm {
      font-size: 1.8rem; }
    @include md {
      font-size: 2.2rem; }
    @include lg {
      font-size: 3rem; } } }
