.mostBox {
  background-color: $colorWhite;
  padding: 3rem 15%;
  font-size: 1rem;

  &__article {
    display: flex;
    border-bottom: 1px solid $colorLightGray;
    @include sm {
      font-size: 1rem; }
    @include md {
      display: block; }

    h2 {
      font-size: 1.4em;
      @include md {
        font-size: 1.6em; }
      @include lg {
        font-size: 2em; } }
    p {
      display: none;
      font-size: 1.6em;

      @include xlg {
        display: block; } } }

  //ugly overwrites
  .tabs .tabs__nav {
    @include lg {
      margin-left: 8rem; }
    .tabs__navItem {
      font-size: 1.2em;
      @include lg {
        font-size: 1.6em; } } } }
